  border:1px dashed lightgray;
  border-radius: 12px;
  overflow: hidden;
  padding: 8px;
  margin-left: 4px;
  box-sizing: border-box;
  box-shadow: 0px 0px 4px lightgray;
  /* gap: 8px; */
}
.userList-item{
  width: 100%;
  height: 30px;
  border-bottom: 2px solid lightgray;
  font-size: 24px;
  font-weight: bolder;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
}
