  text-align: center;
  min-width: 110px;
}
.item_btn{
  display: inline-block;
  width: 100px;
  height: 100%;
  line-height: 55px;
  vertical-align: middle;
  background-color: aquamarine;
  color: white;
  font-size: 36px;
  font-weight: bolder;
  user-select: none;
  cursor: pointer;
}
.loginWrapper{
  display: flex;
  flex-wrap: nowrap;
  height: 100%;
  width:100%;
  justify-content: center;
  align-items: center;
}
.GetinBox{
  width: 800px;
  height: 400px;
  box-shadow: 0px 0px 4px lightgray;
}
.inputBar{
  width: 100%;
  padding:36px;
