  height: 100px;
}
.inputBar input{
  font-size:34px;
  width:100%;
}
.getInBtn_c{
  width: 100%;
  height: 60px;
  text-align: center;
}
.getInBtn_c .b{
  height: 60px;
  width: 60px;
  display: inline-block;
  line-height: 60px;
  vertical-align: middle;
  text-align: center;
  user-select: none;
  cursor: pointer;
  border-radius: 50%;
  background-color: aquamarine;
  color: white;
}
.host_state_container{
  width: 100%;
}
.host_state_item{
  width: 100%;
  height: 44px;
  font-size: 26px;
  /* text-align: center; */
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 16px;
  padding-top: 2px;
  padding-bottom: 2px;
  margin:3px;
  box-sizing: border-box;
  box-shadow: 0px 0px 2px lightgray;
}
</style>
<template>
  <div class="pageHolder">
  <div class="loginWrapper" v-show="!pageState.isLogged">
    <div class="GetinBox">
        <div style="width: 100%;height: 80px;font-size:40px;text-align: center;font-weight: bolder;box-sizing: border-box;padding: 24px;">进入</div>
        <div class="inputBar">
          <input type="text" v-model="pageState.userName" placeholder="用户名">
        </div>
        <div class="inputBar">
          <input type="text" v-model="pageState.adminToken" placeholder="管理员密码(可留空)">
        </div>
        <div class="getInBtn_c">
          <div class="b" @click="getIn">
            →
          </div>
          
        </div>
    </div>
  </div>
  <div class="pageWrapper" v-show="pageState.isLogged">
    <div class="videoContainer" id="vi_C" :style="{width:`${pageState.pageWidth-308}px`}"> 
<!-- Handle Resize Here! -->
    </div>
    <div class="userListContainer">
      <div class="userList-item-funtionHead">
        <div class="userList-item-tag" :class="{isSelect:pageState.tagSelecting==0}" @click="pageState.tagSelecting=0;">
          当前在线用户
        </div>
        <div class="userList-item-tag" v-if="!pageState.adminState" :class="{isSelect:pageState.tagSelecting==1}" @click="pageState.tagSelecting=1;">
          聊天室
        </div>
        <div class="userList-item-tag" v-else :class="{isSelect:pageState.tagSelecting==2}" @click="pageState.tagSelecting=2;">
          管理面板
        </div>
      </div>
      <div class="stContainer" v-if="pageState.tagSelecting==0">
        <div class="userList-item" v-for="(_item,_index) in pageState.userList" :key="'u_'+_index">{{ _item }}</div>   
      </div>
      <div class="stContainer" v-else-if="pageState.tagSelecting==1&&!pageState.adminState">
        施工中   
      </div>
      <div class="stContainer" v-else-if="pageState.tagSelecting==2&&pageState.adminState">
        <div class="adminFunContainer" style="width: 100%;" v-show="pageState.adminToken!=''&& pageState.adminToken!=null && !pageState.isBanned">
          <div class="userList-item">广播视频url</div>
          <input type="text" v-model="pageState.videoUrl" style="width: 100%;height: 40px;font-size: 30px;" />
          <div class="userList-item">
            <label><input type="checkbox" v-model="videoState.isHls"><span>HLS</span></label>
          </div>
          <div class="btn_container">
            <div class="item_btn" @click="brdcastVideoUrl">广播</div>
          </div>
          <div class="userList-item">开关广播视频状态</div>
          <div class="btn_container">
            <div class="item_btn" @click="brdcastVideoState" v-if="!pageState.isBrd">开启</div>
            <div class="item_btn" @click="cancelBrdCast" v-if="pageState.isBrd">关闭</div>
          </div>
        </div>
        
        <div class="userList-item">Host状态</div>
        <div class="host_state_container">
          <div class="host_state_item">进度:{{(signState.timeStamp)}}s</div>
          <div class="host_state_item" v-if="signState.isPause">播放状态:暂停▷ </div>
          <div class="host_state_item" v-else>播放状态:播放中|| </div>
          <!-- <div class="host_state_item">视频源:{{ signState.viUrl }}</div> -->
          <div class="host_state_item" v-if="pageState.isBeBrded">广播状态:正在广播...</div>
          <div class="host_state_item" v-else>广播状态:无广播</div>
          <div class="host_state_item" v-if="signState.isHls">Hls:开启</div>
          <div class="host_state_item" v-else>Hls:关闭</div>
        </div>
        <div class="userList-item">本地状态</div>
        <div class="host_state_container">
          <div class="host_state_item">时差:{{signState.timeDifferent}}s </div>
          <div class="host_state_item" v-if="videoState.isPause">播放状态:暂停▷ </div>
          <div class="host_state_item" v-else>播放状态:播放中|| </div>
          <!-- <div class="host_state_item">视频源:{{ videoState.viUrl }}</div> -->
          <div class="host_state_item" v-if="pageState.isBanned">封禁状态:拒绝响应请求</div>
          <div class="host_state_item" v-else>封禁状态:无</div>
        </div>
        <div class="btn_container">
          <div class="item_btn" @click="changeViState(pageState)" style="width:180px;font-size: 22px;">与Host同步</div>
        </div>
        <div class="userList-item" style="font-size: 16px;">ver:123(2023-5-16 H00)</div>
      </div>
       
    </div>
  </div>
</div>
</template>

<script setup>
import { reactive,onUnmounted,onMounted } from "vue";
import Hls from "hls.js";
import DPlayer from "dplayer";
//https://vip.ffzy-online1.com/20230112/22605_04d87fbc/2000k/hls/mixed.m3u8
let ws=null;
let eventTarget=null;
const pageState=reactive({
  "userName":null,
  "userList":[],
  "pageHeight":window.innerHeight,
  "pageWidth":window.innerWidth,
  "tagSelecting":2,
  "adminState":true,
  "dplayer":null,
  "videoUrl":null,
  "isBrd":false,
  "adminToken":null,
  "isLogged":false,
  "isBanned":false,
  "isBeBrded":false,
  "isBrdCaster":false,
});
const videoState=reactive({
  "isPause":true,
  "timeStamp":0,
  "isHls":false,
  "viUrl":"",
})
const signState=reactive({
  "isPause":true,
  "isHls":false,
  "timeStamp":0,
  "timeDifferent":0,
  "viUrl":"",
})
let ISCONNECTING=false;
const checkUserName=(username)=>{
  const re=/^[A-Za-zs]*$/;
  return re.test(username);
}
const isAllowUserName=(username)=>{
  let newTemp=username.toLowerCase();
  return ((newTemp.indexOf('cs')==-1&&newTemp.indexOf('caoshuo')==-1&&newTemp.indexOf('caos')==-1&&newTemp.indexOf('cshuo')==-1&&newTemp.indexOf('csb')==-1) && (newTemp.indexOf('sb')==-1&&newTemp.indexOf('shabi')==-1&&newTemp.indexOf('chishi')==-1&&newTemp.indexOf('csb')==-1&&newTemp.indexOf('jb')==-1&&newTemp.indexOf('250')==-1))
}
const getIn=()=>{
  if(pageState.userName.length>=3){if(checkUserName(pageState.userName)){if(isAllowUserName(pageState.userName)){
    ws=new WebSocket("ws://81.68.195.96:25252");
    // ws=new WebSocket("ws://localhost:25252");
    pageState.isLogged=true;
    
    ws.onopen=()=>{
    ISCONNECTING=true;
    sayHello();
    }
    ws.onmessage=(_evt)=>{
      const tempData=JSON.parse(_evt.data);
      // console.log(tempData)
      registerFuncs[tempData["trigger"]](tempData);
      if(typeof tempData["action"]!="undefined"){
      actions[tempData["action"]]();
  }
    }
    ws.onclose=()=>{
      quitPage();
    }
    pageState.dplayer=new DPlayer({
        container: document.getElementById("vi_C"),
        theme: '#b7daff', // 风格颜色，例如播放条，音量条的颜色
        loop: false,
        lang: 'zh-cn',
        hotkey: false,
        preload: 'auto',
        mutex: true,
        video: {
          url: '',
          type:"customHls",
          customType:{
            customHls:function(video){
              const hls=new Hls();
              hls.loadSource(video.src);
              hls.attachMedia(video);
            }
          }
        },

      });
    const listenEvts=["play","pause","seeked"];
    for(let i of listenEvts){
      if(pageState["adminToken"]!=null && pageState["adminToken"]!='' && !pageState.isBanned && pageState.isBrdCaster)
        pageState.dplayer.on(i,()=>{
          updateViState();
          ws.send(JSON.stringify({"type":"brdViState","data":{...videoState},"adminToken":pageState["adminToken"]}))
        });
    }
    pageState.dplayer.on("timeupdate",()=>{
      updateViState();
    })}else{
      alert("name包含敏感词！");
    }
  }else{
    alert("name只能包含大小写英文字符！");
  }
}else{
    alert("name应超过三个字符!");
  }
}
//say Hello
const sayHello=()=>{
  ws.send(JSON.stringify({"type":"hello","name":pageState.userName}));
}
const updateViState=()=>{
  if(pageState.dplayer!=null){
    const vi=pageState.dplayer.video;
    videoState.isPause=vi.paused;
    videoState.timeStamp=vi.currentTime;
    videoState.viUrl=pageState.videoUrl;
  }
}
const changeViState=(data)=>{
  // for(let i in data){
  //   signState[i]=data[i];
  // }
  signState.isHls=data.isHls;
  signState.timeStamp=data.timeStamp;
  signState.isPause=data.isPause;
  signState.viUrl=data.viUrl;
  signState.timeDifferent=videoState.timeStamp-signState.timeStamp;
  // console.log(signState);
  if(!pageState.isBrdCaster){
  const vi=pageState.dplayer.video;
  if(videoState.viUrl!=data.viUrl){
    console.log("viUrl:",videoState.viUrl,"hostUrl:",data.viUrl,"data:",data);
    if(data.isHls){
      pageState.dplayer.switchVideo({
        url:data.viUrl,
        type:"customHls",
          customType:{
            customHls:function(video){
              const hls=new Hls();
              hls.loadSource(video.src);
              hls.attachMedia(video);
            }
          }
      })
      pageState.videoUrl=data.viUrl;
      videoState.viUrl=data.viUrl;
      }else{
        pageState.dplayer.switchVideo({
          url:data.viUrl,
        })
        pageState.videoUrl=data.viUrl;
        videoState.viUrl=data.viUrl;        
      }
  }
  if(vi.paused!=data.isPause){
    if(data.isPause){
      vi.pause();
    }else{
      vi.play();
    }
  }
  if(Math.abs(vi.currentTime-data.timeStamp)>3){
    pageState.dplayer.seek(data.timeStamp);
  }
  }
}
//deal with HelloBack Response
const hB=(oriData)=>{
  // const tempData=JSON.parse(oriData);
  uU(oriData);
}
const quitPage=()=>{
  if(ISCONNECTING){
    ws.send(JSON.stringify({"type":"bye","name":pageState.userName}));
    ISCONNECTING=false;
  }
}
const uU=(oriData)=>{
  pageState.userList=oriData.newUsers
}
const uV=(oriData)=>{
  pageState.isBeBrded=true;
  if(oriData.isHls){
  pageState.dplayer.switchVideo({
    url:oriData["url"],
    type:"customHls",
        customType:{
          customHls:function(video){
            const hls=new Hls();
            hls.loadSource(video.src);
            hls.attachMedia(video);
          }
        }
  })}else{
    pageState.dplayer.switchVideo({
    url:oriData["url"],
  })
  }
  pageState.videoUrl=oriData["url"];
}
const sE=(oriData)=>{
  alert(oriData["msg"]);
  
}
const cV=(oriData)=>{
  
  changeViState(oriData["data"]);
}
const cB=(oriData)=>{
  if(oriData["status"]=="sucess"){
    pageState.dplayer.pause();
    pageState.isBeBrded=false;
  }
}
const actions={
  "banned":()=>{alert("服务器发送了一条封禁命令，您的token已被服务器禁止！");pageState.isBanned=true;},
  "beBrdCaster":()=>{pageState.isBrdCaster=true;}
}
const registerFuncs={
  "helloBack":hB,
  "updateUser":uU,
  "changeVideo":uV,
  "showErr":sE,
  "refreshViState":cV,
  "canceled":cB,
}

const handleResize=()=>{
  pageState.pageHeight=window.innerHeight;
  pageState.pageWidth=window.innerWidth;
}
const brdcastVideoUrl=()=>{
  if(ISCONNECTING&&!pageState.isBanned){
    pageState.isBrdCaster=true;
    ws.send(JSON.stringify({"type":"brdCast","url":pageState.videoUrl,"isHls":videoState.isHls,"adminToken":pageState["adminToken"]}));
  }else{
    alert("您未连接到服务器!")
  }
}
const cancelBrdCast=()=>{
  if(ISCONNECTING&&!pageState.isBanned&& pageState.isBrdCaster){
    pageState.isBrd=false;
    pageState.isBrdCaster=false;
    ws.send(JSON.stringify({"type":"cancelBrd","adminToken":pageState["adminToken"]}));
  }
  clearInterval(eventTarget);
  eventTarget=null;
}
const brdcastVideoState=()=>{
  if(ISCONNECTING&&!pageState.isBanned){
    pageState.isBrd=true;
  eventTarget=setInterval(()=>{
    ws.send(JSON.stringify({"type":"brdViState","data":{...videoState},"adminToken":pageState["adminToken"]}));
    pageState.isBrdCaster=true;
  },1000);}else{
    if(eventTarget!=null){
      clearInterval(eventTarget);
      eventTarget=null;
    }
    alert("操作失败!");
  }
}
//https://api.dogecloud.com/player/get.mp4?vcode=5ac682e6f8231991&userId=17&ext=.mp4
onMounted(() => {
    window.addEventListener("beforeunload", () => {ws.send(JSON.stringify({"type":"bye","name":pageState.userName}));});
    window.addEventListener("resize", handleResize);
  })
onUnmounted(() => {
    if(eventTarget!=null){ //remember to check adminToken
      cancelBrdCast();
    }
    window.removeEventListener("beforeunload", () => {ws.send(JSON.stringify({"type":"bye","name":pageState.userName}));});
    window.removeEventListener("resize", handleResize);
})
</script>